<script>
  import { fade, fly } from "svelte/transition";
  let isVisibleRight = true;
  let isVisibleSearchedCertCards = true;
  let generallySearchValue = "";
  const schools = [
    { id: 1, name: "AAA" },
    { id: 2, name: "BBB" },
    { id: 3, name: "BBC" },
    { id: 4, name: "DDA" },
    { id: 5, name: "EEE" },
  ];

  const certs = [
    {
      id: 1,
      studentName: "Nghiêm Đức Hạnh",
      certStatus: "Đã phát",
      school: "Trung học phổ thông chuyên Võ Nguyên Giáp",
      dob: "07/07/1997",
      certType: "Bằng cấp 3",
    },
    {
      id: 2,
      studentName: "Long",
      certStatus: "Đã phát",
      school: "Trung học phổ thông chuyên Võ Nguyên Giáp",
      dob: "07/07/1997",
      certType: "Bằng cấp 3",
    },
    {
      id: 3,
      studentName: "Cường",
      certStatus: "Đã phát",
      school: "Trung học phổ thông chuyên Võ Nguyên Giáp",
      dob: "07/07/1997",
      certType: "Bằng cấp 3",
    },
    {
      id: 4,
      studentName: "Vinh",
      certStatus: "Đã phát",
      school: "Trung học phổ thông chuyên Võ Nguyên Giáp",
      dob: "07/07/1997",
      certType: "Bằng cấp 3",
    },
    {
      id: 5,
      studentName: "Lan",
      certStatus: "Đã phát",
      school: "Trung học phổ thông chuyên Võ Nguyên Giáp",
      dob: "07/07/1997",
      certType: "Bằng cấp 3",
    },
    {
      id: 6,
      studentName: "Hương",
      certStatus: "Đã phát",
      school: "Trung học phổ thông chuyên Võ Nguyên Giáp",
      dob: "07/07/1997",
      certType: "Bằng cấp 3",
    },
    {
      id: 7,
      studentName: "Huyền",
      certStatus: "Đã phát",
      school: "Trung học phổ thông chuyên Võ Nguyên Giáp",
      dob: "07/07/1997",
      certType: "Bằng cấp 3",
    },
  ];
  $: fileredCerts = generallySearchValue
    ? certs.filter((cert) => {
        return cert.studentName
          .toLocaleLowerCase()
          .includes(generallySearchValue.toLocaleLowerCase());
      })
    : certs;
  // function
  let onRightToggle = function () {
    isVisibleRight = !isVisibleRight;
  };

  let onGenerallySearchButtonClick = function () {
    isVisibleSearchedCertCards = true;
    fileredCerts = generallySearchValue
      ? certs.filter((cert) => {
          return cert.studentName
            .toLocaleLowerCase()
            .includes(generallySearchValue.toLocaleLowerCase());
        })
      : certs;
  };

  let onCertCardClick = function (certId) {
    isVisibleSearchedCertCards = !isVisibleSearchedCertCards;
  };
</script>

<style>
  #left-right-container {
    position: relative;
  }
  #left {
  }

  .under-generally-search {
    position: absolute;
    left: 0px;
  }

  #right {
    position: fixed;
    right: 0;
  }

  .cert-card:hover {
    -webkit-box-shadow: 0px 3px 13px -5px rgba(0, 0, 0, 0.67);
    -moz-box-shadow: 0px 3px 13px -5px rgba(0, 0, 0, 0.67);
    box-shadow: 0px 3px 13px -5px rgba(0, 0, 0, 0.67);
    cursor: pointer;
  }
</style>

<div class="row">
  <div class="col-12">
    <div class="page-title-box">
      <div class="page-title-right">
        <ol class="breadcrumb m-0">
          <li class="breadcrumb-item"><a href="()">Trang chủ</a></li>
          <li class="breadcrumb-item" />
          <li class="breadcrumb-item active" />
        </ol>
      </div>
      <h4 class="page-title">Tra cứu văn bằng chứng chỉ</h4>
    </div>
  </div>
</div>

<div class="row" id="left-right-container">
  <div
    class={isVisibleRight ? 'col-lg-8 col-md-7 col-7' : 'col-lg-12 col-md-12 col-12'}
    id="left">
    <div class="card">
      <div class="card-body">
        <div class="row">
          <div class="col-12">
            <div class="row">
              <div class="col-lg-8 form-inline">
                <!-- Date View -->
                <div class="form-group w-100">
                  <input
                    type="text"
                    id=""
                    class="form-control w-100"
                    placeholder="Tìm kiếm..."
                    bind:value={generallySearchValue} />
                </div>
              </div>

              <div class="col-lg-4 text-right">
                <button
                  class="btn btn-success waves-effect waves-light m-1"
                  on:click={onGenerallySearchButtonClick}>
                  <i class="fe-check-circle mr-1" /> Tìm</button>
                <button
                  class="btn btn-light waves-effect waves-light m-1"
                  on:click={onRightToggle}><i class="fe-menu mr-1" /> Mở rộng
                </button>
              </div>
            </div>
          </div>
          <!-- end col-->
        </div>
        <!-- end row -->
      </div>
      <!-- end card-body -->
    </div>

    {#if isVisibleSearchedCertCards}
      <div
        in:fly={{ x: -1000, duration: 1000 }}
        out:fly={{ x: -1000, duration: 1000 }}
        class="under-generally-search col-lg-12">
        {#each fileredCerts as cert, i}
          <div
            class="card-box mb-2 cert-card"
            on:click={onCertCardClick(cert.id)}>
            <div class="row align-items-center">
              <div class="col-lg-10">
                <div class="media">
                  <img
                    class="d-flex align-self-center mr-3 rounded-circle"
                    src="../assets/images/users/user-1.jpg"
                    alt="Generic placeholder image"
                    height="64" />
                  <div class="media-body">
                    <h4 class="mt-0 mb-2 font-16">{cert.studentName}</h4>
                    <div class="row">
                      <div class="col-lg-6">
                        <p class="mb-1">
                          <b>Trạng thái văn bằng: </b>{cert.certStatus}
                        </p>
                        <p class="mb-0"><b>Tên bằng: </b>{cert.certType}</p>
                      </div>
                      <div class="col-lg-6">
                        <p class="mb-1"><b>Ngày sinh:</b>{cert.dob}</p>
                        <p class="mb-0"><b>Trường học:</b> {cert.school}</p>
                      </div>
                    </div>
                  </div>
                </div>
              </div>

              <div class="col-sm-2">
                <div class="text-sm-right">
                  <a href="javascript:void(0);" class="action-icon">
                    <i class="mdi mdi-square-edit-outline" /></a>
                  <a href="javascript:void(0);" class="action-icon">
                    <i class="mdi mdi-delete" /></a>
                </div>
              </div>
              <!-- end col-->
            </div>
            <!-- end row -->
          </div>
        {/each}
      </div>
    {/if}

    {#if !isVisibleSearchedCertCards}
      <div
        in:fly={{ x: 1000, duration: 1000 }}
        out:fly={{ x: 1000, duration: 1000 }}
        class="under-generally-search col-lg-12">
        <div class="card">
          <div class="card-body">
            <div class="row mb-4">
              <div class="col-lg-12">
                <h2><i class="fe-filter" />Lọc</h2>
              </div>
            </div>
            <div class="row">
              <div class="col-lg-12">
                <div class="form-group">
                  <label for="">Họ và tên</label>
                  <input
                    type="text"
                    id=""
                    class="form-control"
                    placeholder="Họ và tên..." />
                </div>
              </div>
            </div>

            <div class="row" />
          </div>
        </div>
      </div>
    {/if}
  </div>

  {#if isVisibleRight}
    <div
      class="col-lg-4 col-md-5 col-5"
      id="right"
      in:fly={{ x: 200, duration: 50 }}
      out:fly={{ x: 200, duration: 50 }}>
      <div class="card">
        <div class="card-body">
          <div class="row mb-4">
            <div class="col-lg-12">
              <h2><i class="fe-filter" />Lọc</h2>
            </div>
          </div>
          <div class="row">
            <div class="col-lg-12">
              <div class="form-group">
                <label for="">Họ và tên</label>
                <input
                  type="text"
                  id=""
                  class="form-control"
                  placeholder="Họ và tên..." />
              </div>
            </div>
          </div>

          <div class="row">
            <div class="col-lg-12">
              <div class="form-group">
                <label for="">Trường học</label>
                <select class="form-control" data-toggle="select2">
                  <option value="-1">Tất cả</option>
                  {#each schools as { id, name }, i}
                    <option value={id}>{name}</option>
                  {/each}
                </select>
              </div>
            </div>
          </div>

          <div class="row">
            <div class="col-lg-6 col-md-12">
              <div class="form-group">
                <label for="">Tuổi</label>
                <input
                  type="text"
                  id=""
                  class="form-control"
                  placeholder="Tuổi..." />
              </div>
            </div>

            <div class="col-lg-6 col-md-12">
              <!-- Date View -->
              <div class="form-group">
                <label for="">Lớp</label>
                <input
                  type="text"
                  id=""
                  class="form-control"
                  placeholder="Lớp..." />
              </div>
            </div>
          </div>

          <div class="row">
            <div class="col-lg-12">
              <div class="form-group">
                <label for="">Trạng thái văn bằng, chứng chỉ</label>
                <select class="form-control" data-toggle="select2">
                  <option value="-1">Tất cả</option>
                  <option value="1">Chưa in</option>
                  <option value="2">Đã in chưa phát</option>
                  <option value="3">Đã phát</option>
                </select>
              </div>
            </div>
          </div>

          <div class="row" />
        </div>
      </div>
    </div>
  {/if}
</div>
